:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

html {
  margin-right: calc(-100vw + 100%);
  overflow-x: hidden;
}

.load-more-section {
  margin-top: 5px;
}

.invisible {
  opacity: 0;
  pointer-events: none;
}

.load-more-section, .search-section {
  width: 90%;
  max-width: 1140px;
  background-color: #c5e2ff;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

button {
  cursor: pointer;
  color: #fff;
  background: #aed3f8;
  border: none;
  border-radius: 2px;
  outline: none;
  font: bold 15px Quicksand, san-serif;
  transition: all .2s;
  display: inline-block;
}

input {
  color: #3081cc;
  background: #dcebff;
  border: none;
  border-radius: 2px;
  outline: none;
}

::-ms-input-placeholder {
  color: #fafafa;
  opacity: 1;
  font: 15px Quicksand, san-serif;
}

::placeholder {
  color: #fafafa;
  opacity: 1;
  font: 15px Quicksand, san-serif;
}

.load-more {
  padding: 8px 16px;
  position: relative;
}

.load-more, .search-form {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 5px 30px;
}

.button--loading:after {
  content: "";
  width: 14px;
  height: 14px;
  border: 4px solid rgba(0, 0, 0, 0);
  border-top-color: #fff;
  border-radius: 50%;
  margin: auto;
  animation: button-loading-spinner 1s infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

